* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
}

li {
  list-style-type: none;
}

a:link,a:visited,a:active,a:hover {
  text-decoration: none;
}

.header {
  padding: 31px 0 30px 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.header__logo {
  width: 201px;
}

.preview {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../images/flower.jpg");
  background-position: 189px 401px;
  background-clip: content-box;
  background-repeat: round;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 460px;
}

.preview__display--img {
  width: 240px;
  height: 200px;
  background: url("../images/default-profile.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.preview__display {
  background-color: #fff;
  font-family: "Montserrat";
  box-shadow: 0 1px 10px grey, 0 1px 3px 0 rgba(0, 0, 0, 0.35);
}

.preview__display--header {
  padding: 20px 0 10px;
}

.preview__display--container {
  padding-left: 20px;
  border-left: 5px solid #438792;
}

.preview__display--title {
  margin-bottom: 4px;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.11;
  color: #114e4e;
  max-width: 195px;
}

.preview__display--subtitle {
  font-size: 16px;
  font-weight: normal;
  color: #54585a;
  max-width: 195px;
  overflow: hidden;
}

.preview .preview__display--footer .menu__list {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 10px;
}

.preview .menu__items {
  border-radius: 50%;
  margin-left: 10px;
  border: 2px solid #a2deaf;
}

.preview .menu__links {
  display: flex;
  width: 25px;
  align-items: center;
  justify-content: center;
  height: 25px;
  text-decoration: none;
  color: #114e4e;
}

.palette0 .preview__display--title {
  color: #114e4e;
}

.palette0 .preview__display--container {
  border-left: 5px solid #438792;
}

.palette0 .menu__links {
  color: #114e4e;
}

.palette0 .menu__items {
  border: 2px solid #a2deaf;
}

.palette1 .preview__display--title {
  color: #420101;
}

.palette1 .preview__display--container {
  border-left: 5px solid #bd1010;
}

.palette1 .menu__links {
  color: #420101;
}

.palette1 .menu__items {
  border: 2px solid #e95626;
}

.palette2 .preview__display--title {
  color: #3e5b65;
}

.palette2 .preview__display--container {
  border-left: 5px solid #eab052;
}

.palette2 .menu__links {
  color: #3e5b65;
}

.palette2 .menu__items {
  border: 2px solid #a0c0cf;
}

.design-box {
  color: #54585a;
  text-transform: uppercase;
  font-family: "Open Sans";
  display: flex;
  border-bottom: #d8d8d8 solid 1px;
  padding-top: 25px;
}

.design-box__title {
  font-size: 14px;
  margin-left: 20px;
  flex-basis: 50%;
  font-weight: normal;
}

.design-box__themes {
  flex-basis: 50%;
}

.design-box__theme {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 20px;
}

.design-box .colors__container {
  display: flex;
  margin-left: 10px;
}

.design-box .colors {
  width: 30px;
  height: 25px;
}

.design-box .colors--green1 {
  background-color: #114e4e;
}

.design-box .colors--green2 {
  background-color: #438792;
}

.design-box .colors--green3 {
  background-color: #a2deaf;
}

.design-box .colors--red1 {
  background-color: #420101;
}

.design-box .colors--red2 {
  background-color: #bd1010;
}

.design-box .colors--red3 {
  background-color: #e95626;
}

.design-box .colors--gray1 {
  background-color: #3e5b65;
}

.design-box .colors--gray2 {
  background-color: #eab052;
}

.design-box .colors--gray3 {
  background-color: #a0c0cf;
}

.share {
  border-bottom: #d8d8d8 solid 1px;
  margin-bottom: 30px;
  padding-top: 25px;
}

.share__btn {
  background-color: #e17334;
  color: #ffffff;
  text-transform: uppercase;
  width: 199px;
  height: 50px;
  border-radius: 12px;
  font-family: "Open Sans";
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px auto;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.32);
}

.share__icon {
  padding-right: 9px;
}

.twitter {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: #d8d8d8 solid 1px;
  padding-bottom: 30px;
}

.twitter__title {
  color: #54585a;
  font-size: 18px;
  font-family: "Open Sans";
  font-weight: 600;
  text-align: center;
  margin-bottom: 5px;
}

.twitter__link {
  text-align: center;
  margin-bottom: 10px;
}

.twitter__link a {
  color: #e17334;
  font-size: 12px;
  font-family: "Open Sans";
  font-weight: 600;
}

.twitter__button {
  height: 30px;
  border-radius: 3px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 14px;
  line-height: 1.43;
  margin: 0 auto;
  background: #00a0f5;
  width: 181px;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);
  text-align: center;
}

.twitter__button--icon {
  font-size: 18px;
  margin: auto 9.5px auto 0px;
}

.twitter__link2 {
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 14px;
}

.filter {
  filter: grayscale(100%);
}

.fill {
  display: flex;
  flex-direction: column;
  border-bottom: #d8d8d8 solid 1px;
  padding-top: 25px;
}

.fill__lable {
  color: #114e4e;
  font-size: 14px;
  font-family: "Open Sans";
  margin-bottom: 7px;
}

.fill__box {
  height: 30px;
  border: #a2deaf solid 1px;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  padding-left: 9px;
  margin-bottom: 20px;
  width: 100%;
}

.fill__image {
  display: flex;
  padding-bottom: 20px;
}

.fill__image--button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  border-radius: 3px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 14px;
  line-height: 1.43;
  width: 140px;
  background: #e17334;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);
}

.fill__image--button:hover {
  cursor: pointer;
}

.fill__image--miniature {
  height: 30px;
  width: 30px;
  border: #a2deaf solid 1px;
  margin-left: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.hidden {
  display: none;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: solid #d8d8d8 1px;
  flex-direction: column;
  padding: 18px 0 20px 0;
  height: 22vh;
}

.footer__paragraph {
  color: #6b6b6b;
  opacity: 0.5;
  font-family: "Open Sans";
  font-size: 14px;
}

.footer__logo {
  width: 103px;
  padding-top: 12px;
}

.footer__logo2 {
  height: 50px;
  padding-top: 12px;
}

.preview__reset {
  height: 30px;
  border-radius: 3px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 14px;
  line-height: 1.43;
  width: 75px;
  border-radius: 15px;
  border: solid 1px #ffffff;
  background-color: transparent;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  color: #54585a;
  align-self: flex-start;
  margin-bottom: 20px;
}

.preview__icon {
  margin-right: 8px;
}

.collapsible {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 7px;
  border-bottom: 1px solid #d8d8d8;
}

.collapsible__left {
  display: flex;
  align-items: center;
}

.collapsible__left--title {
  font-size: 18px;
  color: #54585a;
  font-family: "Open Sans";
  font-weight: 600;
  text-transform: uppercase;
  margin-left: 10px;
}

.collapsible__left--fav {
  color: #e17334;
  font-size: 18px;
}

.collapsible--arrow {
  font-size: 18px;
  color: #54585a;
  transition: transform 0.2s ease;
}

.collapsible.open {
  border-bottom: 1px solid transparent;
}

.collapsible.open .collapsible--arrow {
  transform: rotate(-180deg) translateY(50%);
}

.collapsible:hover {
  cursor: pointer;
}

.landing {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #f5f5f5;
  padding-top: 95px;
  height: 78vh;
}

.landing__image {
  width: 250px;
  height: 63px;
  margin-bottom: 35.4px;
}

.landing__title {
  font-size: 19.2px;
  font-family: "Merriweather";
  font-weight: 900;
  color: #e17334;
}

.landing__text {
  max-width: 264px;
  text-align: center;
  margin-bottom: 17.8px;
  font-size: 12.8px;
  font-weight: normal;
  color: #114e4e;
  font-family: "Merriweather";
}

.landing__menu {
  width: 268px;
  border: rgba(225, 115, 52, 0.21) solid 1.6px;
  border-radius: 4px;
  margin-bottom: 38.6px;
}

.landing__button {
  display: block;
  margin-bottom: 30px;
  background: #438792;
  min-height: 42px;
  width: 129px;
  border-radius: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  text-decoration-line: none;
  text-transform: uppercase;
}

.menu__list {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
}

.menu__item {
  margin-top: 15px;
  width: 60px;
  height: 61px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu__item--fav {
  width: 32px;
  font-size: 22.4px;
  color: #e17334;
  text-align: center;
}

.menu__item--text {
  width: 60px;
  font-size: 12.8px;
  font-family: "Open Sans";
  color: #54585a;
  font-weight: normal;
  text-align: center;
}

.input {
  padding: 30px 20px;
}

@media all and (min-width: 768px) {
  .header {
    padding-left: 40px;
    justify-content: start;
  }

  .preview {
    order: 1;
    width: 50%;
    border-radius: 6px;
    margin: 20px 40px 20px 0;
  }

  .preview__container {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  .footer {
    flex-direction: row;
    justify-content: space-between;
    padding: 18px 40px 22px 41px;
    height: 100%;
  }

  .footer__logo {
    padding-top: 0;
  }

  .footer__logo2 {
    padding-top: 0;
    position: relative;
    left: -48px;
  }

  .landing {
    padding: 340px 0 59px 0;
    height: 100%;
  }

  .landing__image {
    width: 419px;
    height: 105px;
    margin-bottom: 31px;
  }

  .landing__title {
    font-size: 24px;
  }

  .landing__button {
    margin-bottom: 59px;
  }

  .landing__text {
    max-width: 331px;
    margin-bottom: 27px;
    font-size: 16px;
  }

  .landing__menu {
    width: 325px;
    border: rgba(225, 115, 52, 0.21) solid 2px;
    border-radius: 5px;
    margin-bottom: 186px;
  }

  .menu__item {
    width: 75px;
  }

  .menu__item--fav {
    font-size: 28px;
  }

  .menu__item--text {
    width: 75px;
    font-size: 16px;
  }

  .input {
    padding: 30px 30px 30px 40px;
    width: 50%;
  }

  .main {
    display: flex;
    background-image: linear-gradient(to bottom, #f5f5f5, #ffffff);
    min-height: 852px;
  }
}
